.UserForm input:read-only {
  color: var(--transparent-light-2);
}

.title {
  color: var(--main-text-color);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.37;
  padding-top: 72px;
}

.form {
  padding: 32px 0 44px 0;
}

.text {
  display: flex;
  flex-direction: column;
  color: var(--transparent-light-4);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.text_blood {
  margin-bottom: 8px;
}

.input_container {
  display: flex;
  gap: 14px;
  align-items: flex-end;
  margin-bottom: 5px;
}

.input {
  width: 341px;
  height: 52px;
  font-size: 16px;
  margin-top: 8px;
  margin-bottom: 5px;
  padding: 14px;
  border-radius: 12px;
  color: var(--transparent-light-1);
  outline: none;
  background-color: transparent;
  border: 1px solid #efede84d;

  &::placeholder {
    color: var(--transparent-light-2);
  }

  &:focus {
    border: 1px solid #efede84d;
  }
  &:hover {
    border-color: rgba(230, 83, 60, 1);
  }

  &[type='date'] {
    font-family: inherit;
  }
}

.input[type='date']::-webkit-calendar-picker-indicator {
  background-image: none;
  background-color: rgba(239, 237, 232, 1);
  mask-image: url(../../../images/calendar.svg);
  mask-repeat: no-repeat;
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.input[type='date']::-webkit-calendar-picker-indicator:hover {
  background-color: #e6533c;
}



.inputInvalid {
  border-color: red;
}

.input_1 {
  width: 168px;
}

.input_2 {
  width: 159px;
}

.input_3 {
  width: 140px;
}

.input_4 {
  width: 187px;
}

.label {
  color: var(--main-text-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  margin-right: 8px;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.radio {
  margin-right: 8px;
  -webkit-appearance: none;
  appearance: none;
  position: relative;
  cursor: pointer;
}

.radio::before {
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 999999px;
  border: 2px solid gray;
  transition: border-color 0.25s;
}

.radio::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 50%;
  transform: translate(-50%, 50%) scale(0.75);
  display: block;
  opacity: 0;
  width: 11px;
  height: 11px;
  border-radius: 999999px;
  background-color: orangered;
  transition: transform 0.25s, opacity 0.25s;
}

.radio:checked::after {
  opacity: 1;
  transform: translate(-50%, 50%) scale(1);
}

.radio:checked::before {
  border-color: orangered;
}

.label_4 {
  margin-right: 24px;
}

.lifeStyle {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 32px;
  margin-bottom: 48px;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
  width: 144px;
  font-weight: 500;
  font-size: 20px;
  user-select: none;
  background: #e6533c;
  color: #efede8;
  border-radius: 12px;
  border: none;
  cursor: pointer;
}

.button:disabled {
  color: rgba(239, 237, 232, 0.6);
  pointer-events: none;
}

.errorMessage {
  display: flex;
  gap: 3px;
  align-items: center;
  color: red;
  font-size: 12px;
  font-weight: 500;
  height: 12px;
  padding-left: 2px;
}

.errorMessage span::first-letter {
  display: block;
  text-transform: uppercase;
}
